// 使用者填寫資料頁面
<template>
    <div>
      <StepProgress class="step-progress"></StepProgress>
      <div class="row mt-5 justify-content-center">
        <div class="col-8">
        <Form v-slot="{ errors }" @submit="createOrder">
          <div class="row mb-3">
          <label for="inputEmail3" class="col-sm-2 col-form-label">*Email</label>
              <div class="col-sm-10">
                <Field type="email" name="Email" class="form-control" :class="{ 'is-invalid': errors['Email'] }" id="inputEmail3" placeholder="Please enter email (required)" rules="email|required" v-model="form.user.email"></Field>
                <ErrorMessage name="Email" class="invalid-feedback"></ErrorMessage>
              </div>
          </div>
          <div class="row mb-3">
            <label for="inputName3" class="col-sm-2 col-form-label">*Name</label>
              <div class="col-sm-10">
                <Field type="text" name="Name" class="form-control" :class="{ 'is-invalid' : errors['Name'] }" id="inputName3" placeholder="Please enter name (required)" rules="required" v-model="form.user.name"></Field>
                <ErrorMessage name="Name" class="invalid-feedback"></ErrorMessage>
              </div>
          </div>
          <div class="row mb-3">
            <label for="inputPhone3" class="col-sm-2 col-form-label">*Phone</label>
              <div class="col-sm-10">
                <Field type="text" name="Phone" class="form-control" :class="{ 'is-invalid' : errors['Phone'] }" id="inputPhone3" placeholder="Please enter phone (required)" rules="required" v-model="form.user.tel"></Field>
                <ErrorMessage name="Phone" class="invalid-feedback"></ErrorMessage>
              </div>
          </div>
          <div class="row mb-3">
            <label for="inputAddress3" class="col-sm-2 col-form-label">*Address</label>
              <div class="col-sm-10">
                <Field type="text" name="Address" class="form-control" :class="{ 'is-invalid' : errors['Address'] }" id="inputAddress3" placeholder="Please enter address (required)" rules="required" v-model="form.user.address"></Field>
                <ErrorMessage name="Address" class="invalid-feedback"></ErrorMessage>
              </div>
          </div>
          <div class="row mb-3">
            <label for="inputPassword3" class="col-sm-2 col-form-label">commit</label>
            <div class="col-sm-10">
              <textarea class="w-100" name="" id="" cols="30" rows="10" v-model="form.message"></textarea>
            </div>
          </div>
          <div class="row my-5">
            <div class="d-flex justify-content-between">
               <router-link to="/user/cart" class="btn btn-primary p-2">Previous</router-link>
              <button type="submit" class="btn btn-success p-2">Confirm payment</button>
            </div>
          </div>
        </Form>
        </div>
      </div>
    </div>
</template>

<script>
import StepProgress from '../components/StepProgress.vue'
export default {
  data () {
    return {
      form: { // 訂單資料
        user: {
          email: '',
          name: '',
          tel: '',
          address: ''
        },
        message: ''
      },
      orderId: ''
    }
  },
  components: {
    StepProgress
  },
  methods: {
    // 建立訂單，且如成功建立跳轉到結帳頁面
    createOrder () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/order`
      const order = this.form
      this.$http.post(url, { data: order })
        .then((res) => {
          this.$pushMessageState(res, '建立訂單')
          if (res.data.success) {
            this.orderId = res.data.orderId
            this.$router.push(`checkout/${this.orderId}`)
          }
        })
    },
    // 上色
    StepProgress () {
      const circle1 = document.querySelector('.circle1')
      const circle2 = document.querySelector('.circle2')
      const text = document.querySelectorAll('.progress-text')
      const progress = document.getElementById('progress')
      circle1.classList.add('active')
      circle2.classList.add('active')
      text[0].style.color = '#3158d8'
      text[1].style.color = '#3158d8'
      progress.style.width = '50%'
    }
  },
  mounted () {
    this.StepProgress()
  }
}
</script>

<style lang="scss">
@import "../assets/mixins.scss";
.step-progress{
  @include smallpad(){
    display: none
  }
}
</style>
